import React from "react";
import Grow from '@material-ui/core/Grow';
import Box from '@material-ui/core/Box';
import {CardHeader, CardContent, Button} from '@material-ui/core';
import { NewAccount } from "./NewAccount";
import { Match } from "./Match";

export class Introduction extends React.Component {
    constructor(props){
        super(props);
    }

    newAccount = () => {
        //Calling a function of other class (without arguments)
        this.props.router.updateContent(<NewAccount router={this.props.router}/>);
    };
    
    startMatch = () => {
        //Calling a function of other class (without arguments)
        this.props.router.updateContent(<Match router={this.props.router}/>);
    };

    render() {
        return (
            <Grow in={true} timeout={2000}>
                <Box display='flex' flexDirection='column' justifyContent='space-between' height='100%'>
                    <CardHeader title="Welcome"/>
                            <Box mx='30%'  display='flex' flexDirection='column' >
                            <body>{this.props.router.getUserState()}</body>
                            <Button onClick={() => this.startMatch()}>Match Anonymously</Button>
                            <Button onClick={() => this.newAccount()}>Create New Account</Button>
                            </Box> 
                    <Box display='flex' flexDirection='row-reverse'><Button>How it works</Button></Box>
                </Box>
            </Grow>
        )
    }
}