import React, { useState } from "react";
import './Header.css';
import { Match } from './Window/Match';
import { Introduction } from "./Window/Introduction";
import {Button, Link, IconButton, Menu, MenuItem} from "@material-ui/core";
import { AccountCircle } from "@material-ui/icons";
import MenuIcon from '@material-ui/icons/Menu';
import { AccountView } from "./Window/AccountView";

export class Header extends React.Component {

    //For changes when user is logged in
    constructor(props) {
        super(props);
    }

    startMatch() {
        this.props.router.updateContent(<Match router={this.props.router}/>);
    }

    introduction() {
        this.props.router.updateContent(<Introduction router={this.props.router}/>);
    }

    login() {
        this.props.router.updateContent(<Introduction router={this.props.router}/>);
        this.props.router.updateUserState();
    }

    accountView() {
        this.props.router.updateContent(<AccountView router={this.props.router}/>);
    }
    
    render() {
        return(
            <body className="header-bar">
                <div className="header-left">
                    <Button onClick={() => this.startMatch()}>Match Now</Button>
                    <button>Another leftttttttttttttttttttttttttttttt</button>
                    <button>Left</button>
                </div>
                <Link color="#inherit" component="button" className="header-center" onClick={() => this.introduction()}>ruffmix</Link>
                <div className="header-right">
                    <IconButton color="inherit">
                        <MenuIcon />
                    </IconButton>
                    <div hidden={(this.props.router.getUserState() == false) ? false : true}>
                        <Button onClick={() => this.login()}>Login</Button>
                    </div>
                    <div hidden={(this.props.router.getUserState() == false) ? true : false}>
                        <IconButton color="inherit" onClick={() => this.accountView()}>
                            <AccountCircle />
                        </IconButton>
                        <Menu>
                            <MenuItem>Profile</MenuItem>
                            <MenuItem>My account</MenuItem>
                        </Menu>
                    </div>
                </div>
            </body>
        );
    }
}
