import React from 'react';
import {Card, Grow} from '@material-ui/core';
import './MainWindowController.css';
import { Introduction } from './Introduction';
import { Header } from '../Header';

export class MainWindowController extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            content: this.props.content,
            prevContent: "",
            nextContent: "",
            userState: false,
        };

        this.updateContent = this.updateContent.bind(this);
        this.goToPrevious = this.goToPrevious.bind(this);
        this.updateUserState = this.updateUserState.bind(this);
        this.getUserState = this.getUserState.bind(this);
    }

            //Have to bind new funcs

    updateUserState = () => { 
        this.setState({
            userState: true,
        })
    }

    getUserState() {
        return this.state.userState;
    }

    componentDidMount() {
        this.setState({
            //content: <ContentRouter updateCallback={this.updateContent} previousCallback={this.goToPrevious}/>,
            content: <Introduction router={this}/>,
        })
    }

    updateContent(props) {
        const prev = this.state.content;
        this.setState({
            content: props,
            prevContent: prev
        })
    }

    goToPrevious() {
        this.setState({
            content: this.state.prevContent
        })
    }

    render() {
        return (
            <div>
                <Header router={this}/>
                <Grow in={true}>
                    <Card  className="main-window-card">
                        {this.state.content}
                    </Card>
                </Grow>
            </div>
        );
    }
}