import React from "react";
import { Slide, Grow, Box, CardHeader, Stepper, Step, StepLabel, TextField, IconButton,
        FormControlLabel, FormControl, InputLabel, MenuItem, Select, Checkbox, Container, Button } from "@material-ui/core";
import NavigateNextRoundedIcon from '@material-ui/icons/NavigateNextRounded';
import NavigateBeforeRoundedIcon from '@material-ui/icons/NavigateBeforeRounded';
import { NewAccount } from './NewAccount';
import WaveForm from "./WaveForm";

const steps = ['Upload', 'Describe', 'Areas', 'Match', 'Review', 'Feedback'];

const uploadTrack = 
    <Box display='flex' flexDirection='column' textAlign='center'>
        <Box display='flex' flexDirection='row' justifyContent='center' alignSelf='center'>
                    <h2>Upload Your Track</h2>
                </Box>
        <Box p={2}>
            upload area
        </Box>
        <Box p={2}>
            upload area
            
        </Box>
    </Box>

const describeTrack =
    <Box display='flex' flexDirection='column' textAlign='center'>
        <Box display='flex' flexDirection='row' justifyContent='center' alignSelf='center'>
            <h2>Describe Your Track</h2>
        </Box>
        <Box p={2}>
            upload area
        </Box>
        <Box p={2}>
            upload area

        </Box>
    </Box>

const areasOfConcern =
    <Box display='flex' flexDirection='column' textAlign='center'>
        <Box display='flex' flexDirection='row' justifyContent='center' alignSelf='center'>
            <h2>What areas would you like feedback on?</h2>
        </Box>
        <Box p={2}>
            upload area
        </Box>
        <Box p={2}>
            upload area
        </Box>
    </Box>

const matchLoader =
    <Box display='flex' flexDirection='column' textAlign='center'>
        <Box display='flex' flexDirection='row' justifyContent='center' alignSelf='center'>
            <h2>Now Matching. . .</h2>
        </Box>
        <Box p={2}>
            upload area
        </Box>
    </Box>

//set like yourFeedback() to give waveform controller
const reviewTrack =
    <Box display='flex' flexDirection='column' textAlign='center'>
        <Box display='flex' flexDirection='row' justifyContent='center' alignSelf='center'>
            <h2>Review Track</h2>
        </Box>
        <Box p={2}>
            <WaveForm isChild={false} controller={this}/> 
        </Box>
        <Box p={2}>
            upload area

        </Box>
    </Box>

function yourFeedback(propRouter, propRestart) {
    return (
        <Box display='flex' flexDirection='column' textAlign='center'>
            <Box display='flex' flexDirection='row' justifyContent='center' alignSelf='center'>
                <h2>Your Feedback</h2>
            </Box>
            <Box p={2}>
                (insults)
            </Box>
            <Box display='flex' flexDirection='column'>
                <Button
                    onClick={propRestart}
                >
                    Match Again
                </Button>
                <Button
                    onClick={() => propRouter.updateContent(<NewAccount router={propRouter} />)}
                >
                    Create New Account
                </Button>
            </Box>
        </Box>
    );
}

export class Match extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            content: "",
            activeStep: 0,
            activeStepContent: uploadTrack,
            inProp: true,
            subHeader: "",
        };
    }

    restartMatch = () => {
        this.setState({
            activeStep: 0,
            inProp: true,
        })
        this.resetInProp(true);
    }

    resetInProp = (props) => {

        if (props) {
            this.setState({
                inProp: false,
            });
        }
        else {
            this.setState({
                inProp: true,
            });
        }
    }

    returnButton = () => {
        this.props.router.previousHandler();
    };

    backButton = () => {
        const { activeStep } = this.state;

        if (activeStep > 0) {
            this.setState({
                activeStep: activeStep - 1,
                inProp: true,
            });
            this.resetInProp(true);
        }
    }

    nextButton = () => {
        const { activeStep } = this.state;

        if (activeStep < steps.length-1) {
            this.setState({
                activeStep: activeStep + 1,
                inProp: true,
            });
            this.resetInProp(true);
        }
    }

    setActiveStepContent = (step) => {
        switch(step) {
            case 0: {this.setState({activeStepContent: uploadTrack}); break;}
            case 1: {this.setState({activeStepContent: describeTrack}); break;}
            case 2: {this.setState({activeStepContent: areasOfConcern}); break;}
            case 3: {this.setState({activeStepContent: matchLoader}); break;}
            case 4: {this.setState({activeStepContent: reviewTrack}); break;}
            case 5: {this.setState({activeStepContent: yourFeedback(this.props.router, this.restartMatch)}); break;}
            default: return;
        }
    }
    
    render() {
        return (
            <Grow in={true}>
                <Box display='flex' flexDirection='column' height='100%' justifyContent='center'>
                    <Box display='flex' flexDirection='row' justifyContent='center' position='absolute' top='0' alignSelf='center'>
                        <CardHeader title='Match' />
                    </Box>
                    <Box display='flex' alignItems='center' flexDirection='row' justifyContent='space-between' alignItems='center' mx={10}>
                        <Box>
                            <IconButton 
                            disabled={(this.state.activeStep != 0 ? false : true)} 
                            onClick = {() => this.backButton()}
                            >
                                <NavigateBeforeRoundedIcon />
                            </IconButton>
                        </Box>
                        <Box>
                            <Slide 
                                in={this.state.inProp} 
                                onExited={() => this.resetInProp(false)}
                                onEntering={() => this.setActiveStepContent(this.state.activeStep)}
                                direction="up"
                            >
                                {this.state.activeStepContent}
                            </Slide>
                        </Box>
                        <Box>
                            <IconButton 
                            disabled={(this.state.activeStep == 5 ? true : false)} 
                            onClick = {() => this.nextButton()}
                            >
                                <NavigateNextRoundedIcon />
                            </IconButton>
                        </Box>
                    </Box>
                </Box>
            </Grow>
        );
    }
}