import React from "react";
import '../index.css';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

export class ThreeBackground extends React.Component {
  componentDidMount() {
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera( 50, window.innerWidth / window.innerHeight, 0.1, 1500 );

    const renderer = new THREE.WebGLRenderer( {alpha: true} );
    renderer.setClearColor( '#e6eaff', 1 );
    renderer.setSize( window.innerWidth, window.innerHeight );
    document.body.appendChild( renderer.domElement );
    window.addEventListener( 'resize', onWindowResize, false );

    const material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );

    
    let currentGeometry;
    
    switch(Math.floor(Math.random() * 3)) {
      case 0:
        currentGeometry = new THREE.BoxGeometry();
        break;
      case 1:
        currentGeometry = new THREE.ConeGeometry();
        break;
      case 2:
        currentGeometry = new THREE.DodecahedronGeometry();
        break;
      default: 
      currentGeometry = new THREE.BoxGeometry();
        break;
    }

    let currentMesh = new THREE.Mesh(currentGeometry, material);

    camera.position.z = 3;

    scene.add(currentMesh);

    const animate = function () {
      requestAnimationFrame( animate );
      currentMesh.rotation.y += 0.01;
      renderer.render( scene, camera );
    };
    
    function onWindowResize(){
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
      renderer.setSize( window.innerWidth, window.innerHeight );
    }

    animate();

  }

  render() {
    return(true);
  }
} 