import React, { useRef, useEffect  } from 'react';
import WaveSurfer from "wavesurfer.js";
import TimelinePlugin from 'wavesurfer.js/dist/plugin/wavesurfer.timeline.min.js';
import RegionsPlugin from 'wavesurfer.js/dist/plugin/wavesurfer.regions.min.js';
import CursorPlugin from 'wavesurfer.js/dist/plugin/wavesurfer.cursor.min.js';
import './WaveForm.css';
import { Box, Button, IconButton, Chip, Collapse, TextField, Backdrop, InputAdornment } from '@material-ui/core';
import PlayCircleOutlineRoundedIcon from '@material-ui/icons/PlayCircleOutlineRounded';
import PauseCircleOutlineRoundedIcon from '@material-ui/icons/PauseCircleOutlineRounded';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import zIndex from '@material-ui/core/styles/zIndex';
import { div } from 'prelude-ls';
import { Triangle } from 'three';

//hosted: https://upload.wikimedia.org/wikipedia/commons/f/f2/Median_test.ogg

class CommentObject extends React.Component {
    constructor(props) {
        super(props)

        this.state = ({
            comment: this.props.comment,
            user: this.props.user,
            playerObject: this.props.player,
        });
    }

    parsedComment() {
        const commentString = this.state.comment.split(new RegExp('(\\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d\\s|\\d\\:\\d\\d\\-\\d\\:\\d\\d\\s)'));

        const timeRegex = new RegExp('^\\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d\\s|^\\d\\:\\d\\d\\-\\d\\:\\d\\d\\s'
                                        + '| \\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d|\\d\\:\\d\\d\\-\\d\\:\\d\\d '
                                        + '|\\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d$|\\d\\:\\d\\d\\-\\d\\:\\d\\d$'
                                        + '|^\\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d$|^\\d\\:\\d\\d\\-\\d\\:\\d\\d$', 'g');

        const contents = commentString.map((token) => 
            {
                if (token) return (token.match(timeRegex)) 
                ?   <div>
                        <Chip 
                            size="small" 
                            label={token} 
                            clickable
                            variant='outlined'
                            onClick={() => { this.state.playerObject.playFrom(token) }}
                            style={{backgroundColor: 'hsla(200, 50%, 70%, 0.4)', borderColor: 'hsla(200, 50%, 70%, 0.4)'}}
                        />
                    </div> 
                :   <div>{token}</div>
            });

        return contents;
    }

    render() {
        return (
                <Box 
                padding='5px' 
                margin='2px' 
                display='flex' 
                flexDirection='column'
                justifyContent='flex-start' 
                flexWrap='wrap'
                minWidth='96px'
                style={{backgroundColor: 'lightgrey'}}
                borderRadius={4}
                >
                    <div className='user'>{this.state.user}</div>
                    <div className='comment'>{this.parsedComment()}</div>
                </Box>
        );
    }
}

export function MakeWave(args) {
    const waveformRef = useRef();
    const timelineRef = useRef();

    const playerHeight = args.isChild ? 64 : 96;

    useEffect(() => {
        if (waveformRef.current) {
            const wavesurfer = WaveSurfer.create({
                container: waveformRef.current,
                height: playerHeight,
                progressColor: 'lightgrey',
                waveColor: 'lightgreen',
                responsive: 'true',
                plugins: [
                    TimelinePlugin.create({
                        container: timelineRef.current,
                        height: playerHeight,
                        timeInterval: 7.5,
                    }),
                    CursorPlugin.create({
                        showTime: 'true',
                        opacity: '1',

                    }),
                    RegionsPlugin.create({
                        regionsMinLength: 2,
                    })
                ]
            });

            wavesurfer.enableDragSelection({color: 'hsla(400, 100%, 30%, 0.1)', id: 'dragSelection'});

            wavesurfer.on('ready', () => args.handleReady(wavesurfer));
            wavesurfer.on('audioprocess', () => args.setTime());
            wavesurfer.on('seek', () => args.setTime());
            
            wavesurfer.load('https://upload.wikimedia.org/wikipedia/commons/f/f2/Median_test.ogg');
        }
    }, []);

    return (
            <div>
                <Box position='relative' zIndex='5' ref={waveformRef} />
                <div style={{opacity: '20%', marginTop: -playerHeight, pointerEvents: 'none', }} ref={timelineRef}/>
            </div>
    )
}


export default class WaveComponent extends React.Component {
    constructor(props) {
        super(props)
        
        this.state = {
            playState: false,
            expand: false,
            description: '',
            isChild: this.props.isChild,
            commentArr: [],
            commentContent: '',
            inputTime: '',
            playerObject: '',
            playerDuration: '',
            currentTime: '',
            controller: this.props.controller,
            containsPreviews: false,
        };
        this.handleReady = this.handleReady.bind(this);
        this.setTime = this.setTime.bind(this);
    }

    clearPreviewRegions() {
        if (this.state.playerObject) {
            var currentRegions = this.state.playerObject.regions.list;
            Object.keys(currentRegions).forEach(function (key) {
                if (currentRegions[key].id.includes('preview')) currentRegions[key].remove();
            });
        }
    }

    previewRegion() { 
        this.clearPreviewRegions();

        const timeRegex = new RegExp('^\\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d\\s|^\\d\\:\\d\\d\\-\\d\\:\\d\\d\\s'
                                        + '| \\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d|\\d\\:\\d\\d\\-\\d\\:\\d\\d '
                                        + '|\\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d$|\\d\\:\\d\\d\\-\\d\\:\\d\\d$'
                                        + '|^\\d\\d\\:\\d\\d\\-\\d\\d\\:\\d\\d$|^\\d\\:\\d\\d\\-\\d\\:\\d\\d$', 'g');
        var found = this.state.commentContent.match(timeRegex);

        if (found) {
            found.forEach(
                timeString => {
                    const duration = timeString.split('-', 2);
                    const from = duration[0];
                    const to = duration[1];
                    
                    const fromSplit=from.split(":");
                    const startTime=fromSplit[0]*60+fromSplit[1]*1;
                    
                    const toSplit=to.split(":");
                    const endTime=toSplit[0]*60+toSplit[1]*1;
                    
                    const trackDuration = this.state.playerObject.getDuration();
                    if (startTime >= 0 && endTime >= 0 && startTime <= trackDuration && endTime <= trackDuration) {
                        var currentRegions = this.state.playerObject.regions.list;
                        if (currentRegions) {
                            let count = 0;
                            var wasFound = false; 
                            Object.keys(currentRegions).forEach(function (key) {
                                count++;
                                if (currentRegions[key].start === startTime && currentRegions[key].end === endTime && currentRegions[key].id.includes('preview')) {
                                    wasFound = true;
                                }
                            });
                            
                            if (!wasFound) {
                                this.state.playerObject.addRegion({
                                    id: 'preview ' + count,
                                    start: startTime,
                                    end: endTime,
                                    resize: false,
                                    color: 'hsla(400, 100%, 30%, 0.1)'
                                });
                            }
                        }
                    }
                }
            );
        }
 
    }

    checkRegions() {
        const currentRegions = this.state.playerObject.regions.list;
        const comment = this.state.commentContent;

        var previewCheck = false;

        Object.keys(currentRegions).forEach(function (key){
            const start = currentRegions[key].start;
            const end = currentRegions[key].end;
            const startCheck = new Date(start * 1000).toISOString().substr(15, 4);
            const endCheck = new Date(end * 1000).toISOString().substr(15, 4);

            if (currentRegions[key].id.includes('preview')) {
                previewCheck = true;
            }

            if (currentRegions[key].id.includes('preview') && !comment.includes(startCheck + '-' + endCheck)) {
                currentRegions[key].remove();
            }
        });

        this.setState({
            containsPreviews: previewCheck
        })
    }
    
    addRegions() { //hardcoded in handleCommentSubmit
        var currentRegions = this.state.playerObject.regions.list;
        const currentPlayerObject = this.state.playerObject;

        this.checkRegions();

        if (currentRegions) {
            Object.keys(currentRegions).forEach(function (key){
                if (currentRegions[key].id.includes('preview')) {
                    currentPlayerObject.addRegion({ 
                        start: currentRegions[key].start,             
                        end: currentRegions[key].end, 
                        color: 'hsla(200, 50%, 70%, 0.4)',
                        resize: false,
                        drag: false,
                    });

                    currentRegions[key].remove();
                }
            });
        }
    }

    getSeconds(time) {
        const str = time.split(':');
        const minutes = str[0], seconds = str[1];
        const total = parseInt(minutes * 60) + parseInt(seconds);
        return total;
    }

    handleTextChange = (event) => {        
        this.setState({ 
            commentContent: event.target.value,
        })

        this.previewRegion();
        this.checkRegions();
        event.preventDefault();
    }

    handleTimeChange = (event) => {
        this.setState({ 
            inputTime: event.target.value,
        })
        event.preventDefault();
    }

    handleTimeSubmit = (event) => {

        const newCommentStr = this.state.commentContent.concat(this.state.inputTime);
        this.setState({ 
            commentContent: newCommentStr,
            inputTime: ''
        })
        event.preventDefault();
    }

    handleCommentSubmit = (event) => {
        if (this.state.commentContent) {
            this.addRegions();
            const newArr = this.state.commentArr.concat(<CommentObject user='New User' comment={this.state.commentContent} player={this} />);

            this.setState({ 
                commentArr: newArr,
                commentContent: '',
                inputTime: '',
            })
        }
        this.checkRegions();
        event.preventDefault();
    }

    setTime(){
        this.setState({ 
            currentTime: new Date(this.state.playerObject.getCurrentTime() * 1000).toISOString().substr(11, 8),
        })
    }

    handleReady(args) {
        const duration = new Date(args.getDuration() * 1000).toISOString().substr(11, 8);

        this.setState({ 
            playerObject: args,
            playerDuration: duration,
        })
        
        this.state.playerObject.on('region-update-end', (region) => {
            if (region.id == 'dragSelection') {
                const start = region.start;
                const end = region.end;

                const startConvert = new Date(start * 1000).toISOString().substr(15, 4);
                const endConvert = new Date(end * 1000).toISOString().substr(15, 4);

                const newTimeStr = this.state.commentContent.concat(startConvert + '-' + endConvert + ' ');
                this.setState({commentContent: newTimeStr});
                region.remove();
                this.previewRegion();
                this.checkRegions();
            }
        });
        this.checkRegions();

        this.state.playerObject.on('pause', () => {if (this.state.playState != false) this.setState({playState: !this.state.playState})});
    }

    stopPlay() {
        this.setState({
            playState: false,
        })
        this.state.playerObject.pause();
    }

    playFrom(time){
        const playFromTime = time.split('-');
        const from = this.getSeconds(playFromTime[0]); 
        const to = this.getSeconds(playFromTime[1]); 
        this.togglePlay(from, to);
    }

    //Overload w/ (start, end) for range
    togglePlay(start, end) {
        if (start) this.state.playerObject.play(start, end);
        else this.state.playerObject.playPause();

        this.setState({
            playState: (start == 'seek' || start) ? true : !this.state.playState, //weird conditional, change to only (props)?
        })

        if (this.state.controller.getPrevPlayer() == "") {
            this.state.controller.setPrevPlayer(this);
        }
        else if (this != this.state.controller.getPrevPlayer()) {
            this.state.controller.getPrevPlayer().stopPlay();
            this.state.controller.setPrevPlayer(this);
        }

    }

    toggleExpand() {
        this.setState({
            expand: !this.state.expand,
        })
    }

    render() {
        return (
            <Box display='flex' flexDirection='column' padding='10px'>
                <Box 
                style={{display: this.state.isChild ? 'flex' : 'none'}}
                paddingLeft='70%'
                margin='10px'
                >
                    <MoreVertIcon style={{color: 'lightgrey'}} fontSize='small'/>
                </Box>
                <Box 
                display='flex' 
                flexDirection='row'
                justifyContent='flex-start'
                paddingLeft = '20%'
                style={{ display: this.state.isChild ? "none" : "true",}}
                >
                    Song Title
                </Box>
                <Box display='flex' flexDirection='row' alignItems='center' justifyContent='flex-end'>
                    <IconButton
                        onClick={() => { this.togglePlay() }}
                        size='small'
                        edge='start'
                        style={{ marginRight: '5px' }}
                        children={this.state.playState ?
                            <PauseCircleOutlineRoundedIcon style={{ width: this.state.isChild ? '48px' : '64px', height: this.state.isChild ? '48px' : '64px' }}/>
                            : <PlayCircleOutlineRoundedIcon style={{ width: this.state.isChild ? '48px' : '64px', height: this.state.isChild ? '48px' : '64px' }}/>
                            }
                    >
                    </IconButton>
                    <Box width={this.state.isChild ? '60%' : '80%'}>
                        <MakeWave 
                            isChild={this.state.isChild}
                            playState={this.state.playState}
                            handleReady={this.handleReady}
                            setTime={this.setTime}
                        />
                    </Box>
                </Box>
                <Box marginTop='-3%' display='flex' flexDirection='row-reverse' style={{zIndex: '1'}}>{this.state.currentTime}/{this.state.playerDuration}</Box>
                <Box onClick={() => {this.toggleExpand()}} display='flex' flexDirection='row' justifyContent='flex-end' alignItems='center'>
                    <Chip variant="outlined" size="small" label="Comments" />
                    <IconButton 
                    onClick={() => {this.toggleExpand()}}
                    size='small'
                    >
                        <ExpandMoreIcon style={{width: '24px', height: '24px'}}/>
                    </IconButton>
                </Box>
                <Collapse onClick={() => {if (!this.state.expand) this.toggleExpand()}} in={this.state.expand ? true : false} collapsedHeight={15}>
                        <Box 
                        display='flex' 
                        marginLeft= {this.state.isChild ? '40%' : '20%'}
                        flexDirection='column' 
                        alignItems='flex-start' 
                        flexWrap='wrap'
                        >
                            {this.state.commentArr}
                            
                        </Box>
                        <Box display='flex' flexDirection='row' justifyContent='flex-end' alignItems='center' marginTop='5px'>
                            <form onSubmit={this.handleCommentSubmit}>
                                <TextField
                                    label="New Comment"
                                    placeholder='. . .'
                                    multiline
                                    variant="outlined"
                                    value={this.state.commentContent}
                                    onChange={this.handleTextChange}
                                    size='small'
                                />
                                <Button size='small' type='submit'>Submit</Button>
                            </form>
                            <form onSubmit={this.handleTimeSubmit}>
                                <TextField
                                    label="Mins"
                                    placeholder="..."
                                    multiline
                                    variant="outlined"
                                    value={this.state.inputTime}
                                    onChange={this.handleTimeChange}
                                    size='small'
                                />
                                <Button size='small' type='submit'>Time</Button>
                            </form>
                            <Button size='small' onClick={() => this.clearPreviewRegions()} style={{display: this.state.containsPreviews ? 'flex' : 'none'}}>Clear Regions</Button>
                        </Box>
                </Collapse>
            </Box>
        );
    
    }
  }