import React from "react";
import { Slide, Grow, Box, CardHeader, Stepper, Step, StepLabel, TextField, IconButton,
        FormControlLabel, FormControl, InputLabel, MenuItem, Select, Checkbox, Button } from "@material-ui/core";
import NavigateNextRoundedIcon from '@material-ui/icons/NavigateNextRounded';
import NavigateBeforeRoundedIcon from '@material-ui/icons/NavigateBeforeRounded';
import { Introduction } from "./Introduction";

const steps = ['Credentials', 'Personalization', 'Privacy Settings'];

const firstState = 
    <Box display='flex' flexDirection='column'>
        <Box p={2}>
            <TextField size='small' id="outlined-basic" label="Email" variant="outlined"/>
        </Box>
        <Box p={2}>
            <TextField size='small' id="outlined-basic" label="Password" variant="outlined"/>
        </Box>
    </Box>

const secondState = 
    <Box display='flex' flexDirection='column'>
        <Box p={2}>
            <TextField size='small' id="outlined-basic" label="Genre" variant="outlined"/>
        </Box>
        <Box p={2}>
            <TextField size='small' id="outlined-basic" label="Location" variant="outlined"/>
        </Box>
        <Box p={2}>
            <FormControl size='large'>
                <InputLabel id="demo-simple-select-helper-label">Role</InputLabel>
                    <Select
                    labelId="demo-simple-select-helper-label"
                    id="demo-simple-select-helper"
                    >
                        
                        <MenuItem value={10}>Ten</MenuItem>
                        <MenuItem value={20}>Twenty</MenuItem>
                        <MenuItem value={30}>Thirty</MenuItem>
                    </Select>
            </FormControl>
        </Box>
    </Box>

    function thirdState(propRouter) {
        return (
        <Box display='flex' flexDirection='column'>
            <FormControlLabel
                control={<Checkbox/>} 
                label="Secondary"
            />
            <Button
                onClick={() => {propRouter.updateContent(<Introduction router={propRouter} />); propRouter.updateUserState(); }}
            >
                Finish
            </Button>
        </Box>
        );
    }

export class NewAccount extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            content: "",
            activeStep: 0,
            activeStepContent: firstState,
            inProp: true,
        };
    }

    resetInProp = (props) => {

        if (props) {
            this.setState({
                inProp: false,
            });
        }
        else {
            this.setState({
                inProp: true,
            });
        }
    }

    returnButton = () => {
        this.props.router.previousHandler();
    };

    backButton = () => {
        const { activeStep } = this.state;

        if (activeStep > 0) {
            this.setState({
                activeStep: activeStep - 1,
                inProp: true,
            });
            this.resetInProp(true);
        }
    }

    nextButton = () => {
        const { activeStep } = this.state;

        if (activeStep < steps.length-1) {
            this.setState({
                activeStep: activeStep + 1,
                inProp: true,
            });
            this.resetInProp(true);
        }
    }

    setActiveStepContent = (step) => {
        switch(step) {
            case 0: {this.setState({activeStepContent: firstState}); break;}
            case 1: {this.setState({activeStepContent: secondState}); break;}
            case 2: {this.setState({activeStepContent: thirdState(this.props.router)}); break;}
            default: return;
        }
    }
    
    render() {
        return (
            <Grow in={true}>
                <Box display='flex' flexDirection='column' justifyContent='space-between' height='100%' textAlign='center'>
                        <CardHeader title="Create New Account"/>
                        <Box display='flex' flexDirection='row' justifyContent='space-between' alignItems='center' mx={10}>
                            <Box >
                                <div hidden={(this.state.activeStep != 0 ? false : true)} >
                                    <IconButton 
                                    onClick={() => this.backButton()}
                                    >
                                        <NavigateBeforeRoundedIcon />
                                    </IconButton>
                                </div>
                            </Box>
                            <Box>
                                <Slide 
                                    in={this.state.inProp} 
                                    onExited={() => this.resetInProp(false)}
                                    onEntering={() => this.setActiveStepContent(this.state.activeStep)}
                                    direction="up"
                                >
                                    {this.state.activeStepContent}
                                </Slide>
                            </Box>
                            <Box>
                                <div hidden={(this.state.activeStep == 2 ? true : false)}>
                                    <IconButton  
                                    onClick = {() => this.nextButton()}
                                    >
                                        <NavigateNextRoundedIcon />
                                    </IconButton>
                                </div>
                            </Box>
                        </Box>
                        <Stepper
                            activeStep={this.state.activeStep}
                            alternativeLabel
                        >
                            {steps.map((label) => (
                                <Step key={label}>
                                    <StepLabel orientation="vertical">{label}</StepLabel>
                                </Step>
                            ))}
                        </Stepper>
                        
                </Box>
            </Grow>
        );
    }
}