import React from "react";
import { Avatar, Slide, Icon, Fade, CardMedia, Box, TextField, IconButton, Button, Divider,
        Table, TableCell, TableRow, TableBody, Container} from "@material-ui/core";
import { AccountCircle } from "@material-ui/icons";
import './AccountView.css';
import WaveForm from "./WaveForm";

function userInfoContent(propRouter) {
    return (
        <div>
            <Slide in={true} direction="right">
                <Box display='flex' flexDirection='row' alignItems='center'> 
                    <Avatar 
                        style={{marginRight: '10px', width: '96px', height: '96px',}} 
                        src='https://cdn2.iconfinder.com/data/icons/music-studio-2/64/cd-recording-music-audio-studio-512.png' 
                    />
                    <Box 
                    display='flex'
                    flexDirection='column'
                    justifyContent='center'
                    alignItems='flex-start'
                    style={{height: '96px',}}>
                        <Box className='userName' >UserNameText</Box>
                        <Box className='userRole'>Producer</Box>
                        <Box className='userRole'>Atlanta</Box>
                    </Box>
                </Box>
            </Slide>
            <Slide in={true} direction="right">
                <Box display='flex' 
                    flexDirection='column' 
                    alignItems='center' 
                    justifyContent='flex-start' 
                    height='100%'
                >
                    <Box display='flex' marginTop='10px' flexDirection='row'>
                        <Button>Message</Button>
                        <Button>Add To Contacts</Button>
                    </Box>
                    <Table>
                        <TableBody>
                            <TableRow>
                                <TableCell align='left'><b>Genres</b></TableCell>
                                <TableCell>Techno, Trance, House, Jungle, Hardbass</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell align='left'><b>Description</b></TableCell>
                                <TableCell >Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod 
                                    tempor incididunt ut labore et dolore magna aliqua. Ut enim 
                                </TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </Box>
            </Slide>
        </div>
    );
}


export class AccountView extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            userInfoSection: "",
            userTrackSection: "",
            prevPlayer: "",
        };

        this.setPrevPlayer = this.setPrevPlayer.bind(this);
    }

    getPrevPlayer() {
        return this.state.prevPlayer;
    }

    setPrevPlayer = (props) => {
        this.setState({
            prevPlayer: props,
        });
    }

    componentDidMount() {
        this.setState({
            userInfoSection: userInfoContent(this.props.router),
        });

    }

    render() {
        return (
            <div className="account-view">
                <Box className="user-info" style={{ backgroundColor: "" }}>
                    {this.state.userInfoSection}
                </Box>
                <Fade in={true} timeout={1000}>
                    <Box className="user-track" style={{ backgroundColor: "" }}>
                        <WaveForm isChild={false} controller={this}/>
                        <WaveForm isChild={true} controller={this}/>
                        <WaveForm isChild={true} controller={this}/>
                    </Box>
                </Fade>
            </div>
        );
    }
}